import React, { useState, useEffect } from "react";
import classNames from "classnames";
import elv from "../images/elv.png";
import arrow from "../icons/arrow.png";
import css from "./rsvp.less";

import * as firebase from "firebase/app";
require("firebase/firestore");

const RSVP = () => {
  const [code, setCode] = useState("");
  const [invitationCode, setInvitationCode] = useState(null);
  const [data, setData] = useState({
    loading: false,
    names: [],
    attending: [],
    foods: [],
    allergies: "",
    error: null
  });
  const [userData, setUserData] = useState({
    loading: true,
    user: null,
    error: null
  });

  const saveAttending = name => {
    if (!invitationCode || !name) return null;

    const removal = data.attending.includes(name);

    const updatedAttending = removal
      ? data.attending.filter(n => n !== name)
      : [...data.attending, name];

    firebase
      .firestore()
      .collection("data")
      .doc(invitationCode)
      .set({
        ...data,
        attending: updatedAttending,
        foods: removal
          ? data.foods.filter(f => !f.startsWith(`${name}:`))
          : data.foods
      });
  };

  const updateFood = (food, name) => {
    if (!food || !name || !invitationCode) return null;

    const updatedFoods = [
      ...data.foods.filter(f => !f.startsWith(`${name}:`)),
      `${name}:${food}`
    ];

    firebase
      .firestore()
      .collection("data")
      .doc(invitationCode)
      .set({
        ...data,
        foods: updatedFoods
      });
  };

  const onChange = async e => {
    setUserData(userData => ({ ...userData, error: null }));
    const newCode = e.target.value.toUpperCase();

    if (newCode.length <= 5) {
      setCode(newCode);
    }

    if (newCode.length === 5) {
      const email = `${newCode}@marieogdaniel.no`;
      const password = `000-${newCode}`;

      setUserData(userData => ({ ...userData, loading: true }));

      try {
        await firebase.auth().signInWithEmailAndPassword(email, password);
        setUserData(userData => ({ ...userData, loading: false }));
      } catch (err) {
        setCode("");
        setUserData(userData => ({
          ...userData,
          loading: false,
          error: "Ugyldig invitasjonskode"
        }));
      }
    }
  };

  console.log("foods", data.foods);

  useEffect(() => {
    firebase.auth().onAuthStateChanged(user => {
      setUserData(userData => ({ ...userData, loading: false, user }));
    });
  }, []);

  useEffect(() => {
    if (userData.user) {
      setData(prevData => ({ ...prevData, loading: true }));
      const invitationCode = firebase
        .auth()
        .currentUser.email.slice(0, 5)
        .toUpperCase();

      setInvitationCode(invitationCode);

      const doc = firebase
        .firestore()
        .collection("data")
        .doc(invitationCode);

      doc.onSnapshot(
        doc => {
          const data = doc.data();
          setData(prevData => ({
            ...prevData,
            names: data.names ? data.names : [],
            attending: data.attending ? data.attending : [],
            allergies: data.allergies ? data.allergies : "",
            foods: data.foods ? data.foods : [],
            loading: false
          }));
        },
        () => {
          setData(data => ({
            ...data,
            loading: false,
            error: "En feil har oppstått"
          }));
        }
      );
    }
  }, [userData.user]);

  const expandedClass = {
    [css.expanded]: userData.user !== null && !userData.loading && !data.loading
  };

  return (
    <div className={css.outerContainer}>
      <div className={classNames(css.container, expandedClass)}>
        {userData.loading || data.loading ? (
          <div> Laster </div>
        ) : userData.user ? (
          <>
            <div className={css.header}>RSVP</div>
            <div className={css.formContainer}>
              <div className={css.form}>
                <div className={css.formHeader}>Hvem kommer?</div>
                <div>
                  {getNames(data).map(name => (
                    <React.Fragment key={name}>
                      <div
                        className={css.attendingCheckboxAndLabel}
                        onClick={() => saveAttending(name)}
                      >
                        <div
                          className={classNames(css.attendingCheckbox, {
                            [css.checked]: isAttending(data, name)
                          })}
                        />
                        {name}
                      </div>
                      {isAttending(data, name) &&
                        ["Kjøtt", "Fisk"].map(food => (
                          <div
                            key={food}
                            className={css.foodAlternative}
                            onClick={() => updateFood(food, name)}
                          >
                            <div
                              className={classNames(css.radio, {
                                [css.checked]: wantsFood(data, name, food)
                              })}
                            />
                            {food}
                          </div>
                        ))}
                    </React.Fragment>
                  ))}
                </div>
              </div>
              <div className={classNames(css.form, css.informationForm)}></div>
            </div>
            <button onClick={() => firebase.auth().signOut()}>Logg ut</button>
          </>
        ) : (
          <>
            <div className={css.upsell}>Gi beskjed her</div>
            <img src={arrow} alt="" className={css.arrow} />
            <div className={css.header}>RSVP</div>
            <div className={css.description}>
              <div>
                Håper at du har anledning til å være med på feiringen av dagen
                vår
              </div>
              <div>
                Fortell oss om du kan komme eller ikke ved å taste inn din unike
                kode fra innbydelsen
              </div>
            </div>
            <input
              value={code}
              onChange={onChange}
              className={css.inputField}
              placeholder="Skriv her.."
              disabled={userData.loading}
            />
            {userData.error ? (
              <div className={css.error}>{userData.error}</div>
            ) : data.error ? (
              <div className={css.error}>{data.error}</div>
            ) : null}
          </>
        )}
      </div>
      <img
        src={elv}
        alt="Daniel og Marie ved kanal"
        className={classNames(css.elvImage)}
      />
    </div>
  );
};

const getNames = data => {
  return data && data.names ? data.names : [];
};

const isAttending = (data, name) => {
  return data && data.attending && data.attending.includes(name);
};

const wantsFood = (data, name, food) => {
  console.log();
  return data && data.foods && data.foods.some(d => d === `${name}:${food}`);
};

export default RSVP;
